import m from 'mithril'
import { arrowRight, arrowLeft } from './arrows'

export default {
  view ({ attrs: { date } }) {
    return m('.year-container', [
      m('button.year-previous.year-navigation', {
        onclick () {
          const d = new Date(date())
          d.setFullYear(date().getFullYear() - 1)
          date(d)
        }
      }, m(arrowLeft)),
      m('.year', date().getFullYear()),
      m('button.year-next.year-navigation', {
        onclick () {
          const d = new Date(date())
          d.setFullYear(date().getFullYear() + 1)
          date(d)
        }
      }, m(arrowRight))
    ])
  }
}
