import m from 'mithril'
import { setDateClassName, daysBetween, isDateAfter } from './helpers'

export default {
  view ({ attrs: { date, start, end, prelimEnd, options } }) {
    return m('.date', {
      onmouseover () {
        if (start() && start().getTime() < date.getTime()) {
          prelimEnd(date)
        } else {
          prelimEnd(undefined)
        }
      },
      class: setDateClassName(date, start, end, prelimEnd, options),
      onclick () {
        if (options.disableDate(date, start, end)) { return }

        const dayRange = daysBetween(start(), date)

        if (!start()) {
          start(date)
        } else if (start() && !end()) {
          if (isDateAfter(date, start())) {
            if (options.minRange && options.maxRange) {
              if (dayRange >= options.minRange && dayRange <= options.maxRange) {
                end(date)
              } else if (dayRange < options.minRange || dayRange > options.maxRange) {
                start(date)
              }
            } else if (options.minRange) {
              if (dayRange >= options.minRange) {
                end(date)
              } else if (dayRange < options.minRange || dayRange > options.maxRange) {
                start(date)
              }
            } else if (options.maxRange) {
              if (dayRange <= options.maxRange) {
                end(date)
              }
            } else {
              end(date)
            }
          } else {
            start(date)
          }
        } else if (start() && end()) {
          start(date)
          end(null)
        }
      }
    }, [
      m('.number', date.getDate())
    ])
  }
}
