export default {
  // Set the start date value
  initialStart: undefined,
  // Set the end date value
  inititalEnd: undefined,
  // Set the locale for displaying month and day names
  langCode: '',
  // Set whether the picker is permanently open
  open: false,
  // This will make the inputs look disabled and prevent the picker from displaying
  disabled: false,
  // This will set the inputs to be readonly
  readonly: true,
  // Sets the label for the start input
  startInputLabel: '',
  // Sets the label for the end input
  endInputLabel: '',
  // Sets the placeholder for the start input
  startInputPlaceholder: 'Start Date',
  // Sets the placeholder for the end input
  endInputPlaceholder: 'End Date',
  // Sets the minimum allowed range length in days
  minRange: -Infinity,
  // Sets the maximum allowed range length in days
  maxRange: Infinity,
  // Sets whether the picker will close once two dates have been picked
  closeOnDatesSelected: false,
  // Sets whether one month, instead of two, is displayed
  singleMonth: false,
  // Changes start day of the week
  weekStart: 0,
  // Sets the month to be displayed
  initialMonth: undefined,
  // Retrieve the dates from the picker
  onDateSet: ({start, end}) => { },
  // This will run for each date within the view and should return a boolean for whether that date should be disabled
  disableDate: () => false,
  // This will run when any date is picked to format the strings that get displayed within the inputs
  parseDateForInput: d => d.toLocaleDateString(),
  // This will run oninput for both input fields. This function receives a string and should return a date
  onInput: undefined
}
