import './fill-polyfill'
import m from 'mithril'
import stream from 'mithril/stream'
import defaults from './options'
import { nextMonth, prevMonth, checkIfTallRequired, createNullCheckedDisableDate } from './helpers'
import { arrowLeft, arrowRight } from './arrows'
import year from './year'
import month from './month'
import strings from './strings'

export default {
  onremove ({ state }) {
    if (!state.options.open) {
      document.removeEventListener('click', state.close)
    }
  },
  oncreate ({state}) {
    state.show.map(m.redraw)
  },
  oninit ({ state, attrs }) {
    state.options = Object.assign({}, defaults, attrs)
    state.options.disableDate = createNullCheckedDisableDate(state.options.disableDate)

    state.show = stream(state.options.open || false)

    state.toggle = (e) => {
      if (!state.options.open) {
        if (document.activeElement.classList.contains('date-range-input') && !state.show()) {
          state.show(!state.show())
        }
      }
    }

    if (!state.options.open) {
      state.close = () => state.show(false)
      document.addEventListener('click', state.close)
    }

    state.start = stream(state.options.initialStart)
    state.end = stream(state.options.initialEnd)
    state.prelimEnd = stream()

    const startMonth = (initialStart, initialMonth) => {
      if (initialStart) {
        return initialStart
      } else if (initialMonth) {
        return initialMonth
      } else {
        return new Date()
      }
    }

    const monthOne = stream(startMonth(state.options.initialStart, state.options.initialMonth))
    const monthTwo = monthOne.map(() => nextMonth(monthOne()))
    state.months = [monthOne, monthTwo]

    state.end.map(val => {
      if (val) {
        state.options.onDateSet({
          start: state.start(),
          end: state.end()
        })
        if (state.options.closeOnDatesSelected) {
          state.close()
        }
      } else {
        state.options.onDateSet({
          start: null,
          end: null
        })
      }
    })

    state.startParsed = state.start.map(d => d ? state.options.parseDateForInput(d) : '')
    state.endParsed = state.end.map(d => d ? state.options.parseDateForInput(d) : '')

    state.strings = strings(state.options.langCode || window.navigator.userLanguage || window.navigator.language)
  },
  view ({ state: { options, months, show, toggle, start, end, prelimEnd, startParsed, endParsed, strings } }) {
    return m('.date-range', {
      className: options.singleMonth ? 'single-month' : '',
      onclick: e => e.stopPropagation()
    }, [
      m('.range-inputs', [
        m('.input-group', [
          options.startInputLabel.length !== 0 &&
          m('label', options.startInputLabel),
          m('input.date-range-input.first-input', {
            disabled: options.disabled,
            readonly: options.readonly,
            class: (show() ? 'focus ' : '') + (options.disabled ? 'disabled' : ''),
            placeholder: options.startInputPlaceholder,
            onclick: toggle,
            onfocus: toggle,
            value: startParsed(),
            oninput (e) {
              if (options.onInput) {
                const date = options.onInput(e.target.value)
                const isDateDisabled = options.disableDate(date, start, end)
                if (date instanceof Date) {
                  if (!isDateDisabled) {
                    if (date.getMonth() !== months[0]().getMonth() || date.getFullYear() !== months[0]().getFullYear()) {
                      months[0](date)
                    }
                    start(date)
                  }
                } else {
                  throw Error('That is not a date')
                }
              }
            }
          })
        ]),
        m('.input-group', [
          options.endInputLabel.length !== 0 &&
          m('label', options.endInputLabel),
          m('input.date-range-input.second-input', {
            disabled: options.disabled,
            readonly: options.readonly,
            class: (show() ? 'focus ' : '') + (options.disabled ? 'disabled' : ''),
            placeholder: options.endInputPlaceholder,
            onclick: toggle,
            onfocus: toggle,
            value: endParsed(),
            oninput (e) {
              if (options.onInput) {
                const date = options.onInput(e.target.value)
                const isDateDisabled = options.disableDate(date, start, end)
                if (date instanceof Date) {
                  if (start() && !isDateDisabled && (date.getTime() > start().getTime())) {
                    end(date)
                  }
                } else {
                  throw Error('That is not a date')
                }
              }
            }
          })
        ])
      ]),
      show() &&
      m.fragment({}, [
        m('.triangle'),
        m('.calendar-display', {
          className: checkIfTallRequired(months, options.singleMonth, options.weekStart) ? 'tall' : '',
          onbeforeremove ({ dom }) {
            dom.classList.add('fade-out')
            return new Promise(resolve => {
              setTimeout(resolve, 200)
            })
          }
        }, [
          m('button.previous-month.month-navigation', {
            onclick () {
              months[0](prevMonth(months[0]()))
            }
          }, m(arrowLeft)),

          m('button.next-month.month-navigation', {
            onclick () {
              months[0](nextMonth(months[0]()))
            }
          }, m(arrowRight)),

          m(year, {
            date: months[0]
          }),

          m('.months-container', months.map(mon => {
            return m(month, {
              strings,
              options,
              month: mon,
              key: mon().getTime(),
              start,
              end,
              prelimEnd
            })
          }))
        ])
      ])
    ])
  }
}
