export const isYearChange = (o, n) => {
  const thresholdUp = new Date(o)
  thresholdUp.setMonth(o.getMonth() + 6)
  const thresholdDown = new Date(o)
  thresholdDown.setMonth(o.getMonth() - 6)
  const direction = n < o ? 'right' : 'left'

  return (n > thresholdUp && direction === 'left') || (n < thresholdDown && direction === 'right')
}

export const getDaysInMonth = (month, year) => {
  var date = new Date(year, month, 1)
  var days = []
  while (date.getMonth() === month) {
    const d = new Date(date)
    days.push(d)
    date.setDate(date.getDate() + 1)
  }
  return days
}

export const createNullCheckedDisableDate = (fn) => {
  return (date, start, end) => {
    return fn(dOrD(date), dOrD(start()), dOrD(end()))
  }
}

export const setDateClassName = (date, start, end, prelimEnd, options) => {
  const classes = []

  if (options.disableDate(date, start, end)) { classes.push('date-selection-disabled') }

  let prelimEndDisabled = prelimEnd() && options.disableDate(prelimEnd(), start, end)

  if (options.minRange || options.maxRange) {
    const daysBetweenDates = daysBetween(start(), date)
    const daysBetweenPrelim = daysBetween(start(), prelimEnd());

    [ x => x < options.minRange, x => x > options.maxRange ].forEach(compare => {
      prelimEndDisabled = compare(daysBetweenPrelim) || prelimEndDisabled
      if (date > start() && compare(daysBetweenDates)) {
        classes.push('date-selection-disabled')
      }
    })
  }

  const isDateStart = isDateSame(start(), date)
  const isDateEnd = isDateSame(end(), date)
  const isDateInBetween = isDateBefore(date, end()) && isDateAfter(date, start())
  const isDateInBetweenPrelim = isDateBefore(date, prelimEnd()) && isDateAfter(date, start()) && !prelimEndDisabled

  if (isDateStart) {
    classes.push('first-date')
  } else if (isDateEnd) {
    classes.push('second-date')
  } else if (isDateInBetween || (isDateInBetweenPrelim && !end())) {
    classes.push('in-between')
  }

  return classes.join(' ')
}

export const prevMonth = date => {
  const newDate = new Date()
  newDate.setDate(1)
  if (date.getMonth() > 0) {
    newDate.setFullYear(date.getFullYear())
    newDate.setMonth(date.getMonth() - 1)
  } else {
    newDate.setMonth(11)
    newDate.setFullYear(date.getFullYear() - 1)
  }
  return newDate
}

export const nextMonth = date => {
  const newDate = new Date()
  newDate.setDate(1)
  if (date.getMonth() < 11) {
    newDate.setFullYear(date.getFullYear())
    newDate.setMonth(date.getMonth() + 1)
  } else {
    newDate.setMonth(0)
    newDate.setFullYear(date.getFullYear() + 1)
  }
  return newDate
}

export const checkIfTallRequired = (months, singleMonth, weekStart) => {
  const bools = months.map(mo => {
    const month = mo()
    const days = getDaysInMonth(month.getMonth(), month.getFullYear())
    const startDay = adjustPreMonthEmpties(days[0].getDay(), weekStart)
    const total = startDay + days.length
    return total > 35
  })
  if (window.innerWidth < 768 || singleMonth) {
    return bools[0]
  } else {
    return bools.includes(true)
  }
}

export const daysBetween = (date1, date2) => {
  const d1 = new Date(date1)
  const d2 = new Date(date2)
  var timeDiff = Math.abs(d2.getTime() - d1.getTime())
  return Math.ceil(timeDiff / (1000 * 3600 * 24)) + 1
}

export const adjustPreMonthEmpties = (startDay, weekStart) => {
  if (startDay - weekStart < 0) {
    const toMinus = weekStart - startDay
    return 7 - toMinus
  } else {
    return startDay - weekStart
  }
}

export const isDateSame = (date1, date2) => {
  const d1 = new Date(date1)
  const d2 = new Date(date2)
  d1.setHours(12, 0, 0, 0)
  d2.setHours(12, 0, 0, 0)

  return d1.getTime() === d2.getTime()
}

export const isDateBefore = (first, second) => {
  const d1 = new Date(first)
  const d2 = new Date(second)
  d1.setHours(12, 0, 0, 0)
  d2.setHours(12, 0, 0, 0)

  return d1.getTime() < d2.getTime()
}

export const isDateAfter = (first, second) => {
  const d1 = new Date(first)
  const d2 = new Date(second)
  d1.setHours(12, 0, 0, 0)
  d2.setHours(12, 0, 0, 0)

  return d1.getTime() > d2.getTime()
}

export const dOrD = (d) => {
  const date = new Date(d)
  if (!isNaN(date.getTime())) {
    return date
  } else {
    return null
  }
}
