import m from 'mithril'
import { isYearChange, getDaysInMonth, adjustPreMonthEmpties } from './helpers'
import date from './date'

export default {
  onbeforeremove ({ dom, state, attrs: { month } }) {
    if (!isYearChange(state.initialDate, month())) {
      const direction = month() < state.initialDate ? 'right' : 'left'
      dom.classList.add('out-' + direction)
      dom.parentElement.classList.add('in-' + direction)
      return new Promise(resolve => {
        setTimeout(() => {
          dom.parentElement.classList.remove('in-' + direction)
          resolve()
        }, 300)
      })
    }
  },
  oninit ({ state, attrs: { month, options, strings } }) {
    state.initialDate = month()

    state.dayStrings = strings.daysShort.slice(0)
    if (options.weekStart) {
      const moveBack = state.dayStrings.slice(0, options.weekStart)
      const moveUp = state.dayStrings.slice(options.weekStart, state.dayStrings.length)

      state.dayStrings = moveUp.concat(moveBack)
    }

    state.days = getDaysInMonth(month().getMonth(), month().getFullYear())
    state.startDay = state.days[0].getDay()
    state.empties = new Array(adjustPreMonthEmpties(state.startDay, options.weekStart)).fill(0)
  },
  view ({ state: { dayStrings, empties, days }, attrs: { options, month, start, end, prelimEnd, strings } }) {
    return m('.month', {
      onmouseout () {
        prelimEnd(undefined)
      }
    }, [
      m('h5', strings.months[month().getMonth()]),
      m('.month-grid', [
        m('.days', dayStrings.map((d, i) => {
          return m('.day', dayStrings[i])
        })),
        m('.dates', [
          empties.map(() => {
            return m('.empty-date')
          }),
          days.map(d => {
            return m(date, {
              date: d,
              start,
              end,
              prelimEnd,
              options
            })
          })
        ])
      ])
    ])
  }
}
