
const initDate = () => {
  const d = new Date()
  d.setMonth(0)
  const day = d.getDay()
  const diff = d.getDate() - day + (day === 0 ? -7 : 0)
  return new Date(d.setDate(diff))
}

const removeTrailing = (str) => {
  if (str.slice(-1) === ',' || str.slice(-1) === '.') {
    return str.slice(0, -1)
  } else {
    return str
  }
}

export default function strings (lang) {
  const days = new Array(7).fill(0)
  const months = Array(12).fill(0)

  let startDate = initDate()
  const monthStrings = months.map(() => {
    var options = {month: 'long'}
    const parsed = startDate.toLocaleDateString(lang, options)
    startDate.setMonth(startDate.getMonth() + 1)
    return removeTrailing(parsed)
  })

  startDate = initDate()
  const monthShortStrings = months.map(() => {
    var options = {month: 'short'}
    const parsed = startDate.toLocaleDateString(lang, options)
    startDate.setMonth(startDate.getMonth() + 1)
    return removeTrailing(parsed)
  })

  startDate = initDate()
  const dayStrings = days.map(() => {
    var options = {weekday: 'long'}
    const parsed = startDate.toLocaleDateString(lang, options)
    startDate.setDate(startDate.getDate() + 1)
    return removeTrailing(parsed)
  })

  startDate = initDate()
  const dayShortStrings = days.map(() => {
    var options = {weekday: 'short'}
    const parsed = startDate.toLocaleDateString(lang, options)
    startDate.setDate(startDate.getDate() + 1)
    return removeTrailing(parsed)
  })

  return {
    months: monthStrings,
    monthsShort: monthShortStrings,
    days: dayStrings,
    daysShort: dayShortStrings
  }
}

strings()
